金之績效，基金投資風險請詳閱基金公開說明書。有關基金運用限制及投資風險、配息機制、基金申購手續費之收取方式，比率、投資人所需負責之直接或間接相關費用及基金應負擔之相關費用，已揭露於基金公開說明書中，投資人可向元大投信及基金之銷售機構索取，或至公開資訊觀測站及元大投信網站（https://www.yuantafunds.com）中查詢。為避免因受益人短線交易頻繁，造成基金管理及交易成本增加，進而損及基金長期持有之受益人權益，元大系列基金不歡迎受益人進行短線交易。基金非存款或保險，故無受存款保險、保險安定基金或其他相關保障機制之保障。元大系列基金之配息率不代表基金報酬率，且過去配息率不代表未來配息率，基金淨值可能因市場因素而上下波動。 ")]),_c('li',[_vm._v(" 【元大台灣高股息優質龍頭基金("),_c('span',{staticClass:"has-underline"},[_vm._v("本基金之配息來源可能為收益平準金")]),_vm._v(")】基金配息可能由基金的收益平準金中支付。任何涉及由收益平準金支出的部分可能導致原始投資金額減損。收益平準金係指本基金成立日起，計算日之每日受益權單位淨資產價值中，相當於原受益人可分配之收益金額。本基金持股之股利配發時間及金額視各別企業而定。主要來自於「高股息優質龍頭」企業發放的現金股利於累積一段期間後，分為12個月，每月分配給投資人。另每月之收益分配係以本基金B 類型受益權單位投資於中華民國境內所得之現金股利、利息收入、已實現股票股利、子基金收益分配及收益平準金等收入扣除應負擔之費用後之可分配收益且不需扣除已實現及未實現之資本損失。每年度分配收益係以本基金B 類型受益權單位投資於中華民國境內所得之已實現資本利得扣除資本損失（包括已實現及未實現之資本損失），並扣除應負擔之費用後之可分配收益。經理公司認為有必要（如市況變化足以對相關基金造成影響）可適時調整收益分配金額，惟本基金B 類型受益權單位可分配收益，於經理公司決定收益分配金額後，若有未分配收益得累積併入以後之可分配收益。B 類型受益權單位之任一受益人應得之分配金額為新臺幣伍佰元（含）以下者，受益人（除基金銷售機構以自己名義受託申購基金專戶、特定金錢信託專戶、財富管理專戶或壽險公司透過投資型保單等方式為投資人申購本基金者外）同意授權經理公司得以該收益分配金額再申購本基金B 類型受益權單位作為當期收益分配之給付方式，該等收益分配金額再申購本基金之申購手續費為零元。 ")]),_c('li',[_vm._v(" 基金注意事項： "),_c('div',[_vm._v(" (1)銀行辦理信託業務，應盡善良管理人之注意義務及忠實義務。 "),_c('div',[_vm._v("(2)銀行不擔保信託業務之管理或運用績效，委託人及受益人應自負盈虧。")]),_c('div',[_vm._v("(3)信託財產經運用於存款以外之標的者，不受存款保險之保障，投資人申購前應仔細閱讀行銷文件。")]),_c('div',[_vm._v("(4)基金並非存款，基金投資具有投資風險，可能使本金發生虧損。")]),_c('div',[_vm._v("(5)投資人因不同時間進場，將有不同之投資績效，過去之績效亦不代表未來績效之保證。")]),_c('div',[_vm._v("(6)如基金投資以外幣計價之有價證券，其淨值可能因匯率而變動。")]),_c('div',[_vm._v(" (7)現階段法令規定境外基金投資大陸地區證券市場之有價證券以掛牌上市有價證券及銀行間債券市場為限，且投資總金額不得超過該基金淨資產價值20%(境外基金機構依鼓勵境外基金深耕計畫向金管會申請並經認可，得放寬投資總金額上限至40%)，故並非完全投資於大陸地區之有價證券；投信基金投資大陸地區之有價證券雖可包括其證券集中交易市場交易之股票、存託憑證或符合一定信用評等之債券等且法令並無規範投資總金額之上限，惟仍應符合信託契約、公開說明書、證券投資信託基金管理辦法等相關規定及受到基金公司QFII額度之限制，故亦非一定完全投資於大陸地區有價證券。又投資人亦須留意中國市場政府政策、法令、會計稅務制度、經濟與市場等變動所可能產生之投資風險。 ")]),_c('div',[_vm._v(" (8)中國境內債券之信用評級，由中國評級機構所授予，其所使用評級標準與方法可能與其他國際評級機構所採用者不一致，故該評級制度未必可提供同等的標準與獲得國際評級的證券進行比較。 ")]),_c('div',[_vm._v(" (9)由於新興市場基金主要投資風險，除包含一般股票型基金之投資組合跌價與匯率風險外，與成熟市場相比，須承受較高之政治與金融管理風險，且因市值及制度性因素，流動性風險也相對較高，故新興市場投資組合波動性普遍高於成熟市場。 ")]),_c('div',[_vm._v(" (10)投資新興市場可能比投資已開發國家有較大的價格波動及流動性較低的風險。部分基金可能持有衍生性商品部位，可能達基金淨資產價值之100%，可能造成基金淨值高度波動及衍生其他風險，委託人應慎選投資標的。 ")]),_c('div',[_vm._v(" (11)基金配息率不代表基金報酬率，且過去配息率不代表未來配息率；基金淨值可能因市場因素而上下波動。基金的配息可能由基金的收益或本金中支付。任何涉及由本金支出的部分，可能導致原始投資金額減損。基金之配息來源可能為本金。 ")]),_c('div',[_vm._v(" (12)非投資等級債券基金(本類基金主要係投資於非投資等級之高風險債券且配息來源可能為本金)(主要特色係投資前揭基金之組合基金亦同)，適合能承受較高風險之『非保守型』委託人且不宜占投資組合過高的比重。由於非投資等級債券之信用評等未達投資等級或未經信用評等，且對利率變動的敏感度甚高，可能會因利率上升、市場流動性下降，或債券發行機構違約不支付本金、利息或破產而蒙受虧損，不適合無法承擔相關風險之投資人；又如係本國投信機構募集者，則可能投資於符合美國Rule 144A規定具有私募性質之債券，雖其投資總金額不得超過基金淨資產價值之百分之三十，然該債券具有債券發行人違約之信用風險、利率風險、流動性風險、交易對手風險及因財務訊息揭露不完整或因價格不透明導致波動性較大之風險。 ")]),_c('div',[_vm._v(" (13)本專案活動之基金經金管會核准或同意生效，惟不表示絕無風險。基金經理公司以往之經理績效不保證基金之最低投資收益；基金經理公司除盡善良管理人之注意義務外，不負責基金之盈虧，亦不保證最低之收益，投資人申購前應詳閱基金簡式公開說明書及基金公開說明書。 ")]),_c('div',[_vm._v(" (14)委託人之交易如經基金公司認定為短線交易者，基金公司或本行可拒絕受理申請轉換或申購要求，並可請求支付一定比例之贖回費用或其他相關費用，費用標準均依各該基金公司之規定。 ")]),_c('div',[_vm._v(" (15)後收型基金在贖回時，基金公司將依持有期間長短收取不同比率之遞延申購手續費，該費用將自贖回總額中扣除；另各基金公司需收取一定比率之分銷費用，將反映於每日基金淨值中，投資人無需額外支付。 ")]),_c('div',[_vm._v(" (16)有關投資基金應承擔之相關風險(如國家風險、市場風險、貨幣風險、匯率風險、信用風險、債券風險等)及負擔之費用(含分銷費用)，已揭露於基金之公開說明書或投資人須知中，投資人欲索閱基金公開說明書(或其中譯本)或投資人須知，可至本行各分行洽詢理財專員或洽該基金公司，或至公開資訊觀測站或境外基金資訊觀測站(https://www.fundclear.com.tw)查詢。 ")])])])]),_c('div',{staticClass:"notice_content_title"},[_vm._v("（六）國內ETF投資警語：")]),_c('ol',{attrs:{"data-aos":"fade-down"}},[_c('li',[_vm._v("市場風險：ETF的價格會因經濟、政治、貨幣、法律等各種影響市場因素而波動。")]),_c('li',[_vm._v(" 集中度風險：ETF通常分散持有一籃子投資組合，較不受個別標的風險影響，然而，有些ETF仍可能有曝險集中於少數個股、集中投資於特定產業或單一商品的情形。 ")]),_c('li',[_vm._v(" 流動性風險：ETF流動量提供者負責提供ETF買賣報價，方便投資人買賣ETF。儘管ETF大部分有一個或以上的流動量提供者，但若有流動量提供者失責或停止履行報價義務，投資人可能會有買不到或是賣不掉ETF的風險。 ")]),_c('li',[_vm._v(" 折溢價風險：ETF市價由市場供需決定，可能低於或高於淨值而產生折價或溢價。折溢價過大代表ETF成交價格產生偏離。此外，連結海外標的之ETF可能因國內外市場交易時間差而有折溢價情形。 ")]),_c('li',[_vm._v(" 追蹤誤差風險：追蹤誤差係指ETF報酬率與標的指數報酬率的差異程度。產生追蹤誤差的原因很多，包括基金須支付的費用及支出影響、基金資產與指數成分股之差異、基金的計價貨幣、交易貨幣及投資所用的貨幣間的匯率差價，ETF投資組合的成分股配股配息、基金經理人所使用的追蹤工具及複製策略等，皆會造成ETF的資產淨值與股價指數間存在落差。 ")]),_c('li',[_vm._v(" 終止上市風險：發行人可能因ETF資產規模太小、單位淨值過低等信託契約規定的終止情事，報請主管機關核准終止信託契約並申請下市。 ")])])]),_c('div',{staticClass:"footer_address"},[_c('div',[_vm._v("【元大銀行總址地址：臺北市松山區敦化南路一段66號；聯絡電話：(02)2173-6699】")]),_c('div',[_vm._v("詳情歡迎親洽本行各分行理財業務專員，或撥0800-688-168，或至本行網站：www.yuantabank.com.tw查詢。")])])])]),_c('div',{staticClass:"footer"},[_c('div',{staticClass:"pb-2"},[_vm._v("Copyright © Yuanta Commercial Bank Co. Ltd. All Rights Reserved.")]),_c('div',[_vm._v("地址：10557 台北市松山區敦化南路一段 66 號 ｜ 電子信箱：Service@yuanta.com ｜ 客服專線：0800-688-168")])]),_c('ul',{staticClass:"open_account"},[_c('li',[_c('a',{staticClass:"no-underline",attrs:{"href":"https://ebank.yuantabank.com.tw/ecntr/tx/opentrustacct?method=login","target":"_blank"}},[_c('div',{staticClass:"open_account_card d-flex align-items-center justify-content-center"},[_c('img',{attrs:{"src":require("../assets/image/sidebar1.png"),"alt":"icon"}}),_c('div',{staticClass:"account_text"},[_vm._v("信託開戶")])])])]),_c('li',[_c('a',{staticClass:"no-underline",attrs:{"href":"https://ebank.yuantabank.com.tw/ecntr/tx/openacct","target":"_blank"}},[_c('div',{staticClass:"open_account_card d-flex align-items-center justify-content-center"},[_c('img',{attrs:{"src":require("../assets/image/sidebar2.png"),"alt":"icon"}}),_c('div',{staticClass:"account_text"},[_vm._v("線上開戶")])])])])])])}]

